<template>
    <div>
        <h2>入廊管线列表</h2>
        <div class="tunnelList">
            <Menu :open-names="['1']" accordion>
                <Submenu name="1">
                    <template slot="title">
                        <!-- {{item.affiliatedCompanyName}} -->
                        内容管理
                    </template>
                    <MenuItem name="1-1">文章管理</MenuItem>
                    <MenuItem name="1-2">评论管理</MenuItem>
                    <MenuItem name="1-3">举报管理</MenuItem>
                </Submenu>
                <Submenu name="2">
                    <template slot="title">
                        用户管理
                    </template>
                    <MenuItem name="2-1">新增用户</MenuItem>
                    <MenuItem name="2-2">活跃用户</MenuItem>
                </Submenu>
            </Menu>
        </div>
        <div class="areaDetails"></div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            wareHouse:[
                {
                    id:1,
                    name: '综合仓',
                    tunnelList:[
                        {
                            tunnelId: 1,
                            affiliatedCompany: [
                                {
                                    affiliatedCompanyName: '中国移动',
                                },
                                {
                                    affiliatedCompanyName: '中国联通'
                                },
                                {
                                    affiliatedCompanyName: '中国电信'
                                },
                                {
                                    affiliatedCompanyName: '南宁水务'
                                }
                            ],
                        }
                    ]
                }
            ]
        }
    }
}
</script>
<style scoped>
.tunnelList,.areaDetails{
    display: inline-block;
    border: 1px solid #eeeeee;
    border-radius:8px; 
    height: 72vh;
    margin-top: 5px;
    vertical-align: top;
}
.tunnelList{
    width: 20%;
}
.areaDetails{
    width: 79%;
}
</style>



