<template>
    <div class="SimplePie" :id="id"></div>
</template>
<style scoped>
.SimplePie {
  position: relative;
  height: 100%;
  width: 100%;
}
</style>
<script>
export default {
  name: "SimplePie",
  props: {
    id: {
      default: "./logo.png"
    },
    requestUrl: {
      default: "./logo.png"
    },
    title: {
      default: "SimplePie"
    }
  },
  data() {
    return {
      myChart: {},
      visualMapArr:[],
      option: {
        backgroundColor: '#eeeeee',
        title: {
          text: "Customized Pie",
          left: "left",
          textStyle: {
            color: '#2e739b'
          }
        //   top: 20
          //   textStyle: {
          //     color: "#ccc"
          //   }
        },

        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },

        visualMap: {
          show: false,
          min: '',
          max: '',
          inRange: {
            colorLightness: [0, 1]
          }
        },
        series: [
          {
            name: "各类型设备数",
            type: "pie",
            radius: "55%",
            center: ["50%", "50%"],
            data: [
            ].sort(function(a, b) {
              return a.value - b.value;
            }),
            roseType: "radius",
            label: {
              normal: {
                textStyle: {
                  color: "#2c343c"
                }
              }
            },
            labelLine: {
              normal: {
                lineStyle: {
                  color: "#2c343c"
                },
                smooth: 0.2,
                length: 10,
                length2: 20
              }
            },
            itemStyle: {
              normal: {
                color: "#c23531",
                // shadowBlur: 200,
                // shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            },

            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function(idx) {
              return Math.random() * 200;
            }
          }
        ]
      }
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.drawBar();
      this.fetchData(this.requestUrl);
    },
    drawBar() {
      let _this = this;
      _this.myChart = _this.$echarts.init(document.getElementById(_this.id));
      _this.option.title.text = _this.title;
      _this.myChart.setOption(_this.option);
      window.addEventListener("resize", _this.myChart.resize);
    },
    fetchData(requestUrl) {
        let _this = this;

      _this.axios.get(requestUrl).then(result => {
        let { code, data } = result.data;
        if (code == 200) {
            _this.option.series[0].data = [];
            data.forEach(element => {
                _this.option.series[0].data.push({
                    name: element.key,
                    value: element.val,
                });
                this.visualMapArr.push(element.val)
                _this.option.visualMap.min=Math.min.apply(null, this.visualMapArr)
                _this.option.visualMap.max=Math.max.apply(null, this.visualMapArr)
            });
            _this.myChart.setOption(_this.option)
        }
      });
    }
  }
};
</script>



