<template>
    <div>
        <div class="conditions">
            <Row>
                <Col span="6">
                    <span class="conditionTitle">付款方式：</span>
                    <Select v-model="conditions.payment" @on-change='queryList()' style="width:60%">
                        <Option value=null>所有</Option>
                        <Option v-for="item in payType" :value="item.val" :key="item.id">{{ item.key }}</Option>
                    </Select>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">所属公司：</span>
                    <Select v-model="conditions.companyId" @on-change="queryList()" style="width: 60%">
                        <Option value="null">所有</Option>
                        <Option v-for="item in company" :key="item.id" :value="item.id">{{item.name}}</Option>
                    </Select>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">联系人：</span>
                    <Input v-model="conditions.contact" placeholder="请输入联系人姓名" style="width: 60%" @on-keyup="queryList()"></Input>
                </Col>
            </Row>
            <Row style="margin-top: 10px">
                <Col span="6">
                    <span class="conditionTitle">开始时间：</span>
                    <DatePicker type="datetime" placeholder="请选择开始时间" style="width: 60%" v-model="conditions.startTime" @on-change="queryList()"></DatePicker>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">结束时间：</span>
                    <DatePicker type="datetime" placeholder="请选择结束时间" style="width: 60%" v-model="conditions.endTime" @on-change="queryList()"></DatePicker>
                </Col>
            </Row>
        </div>
        <div class="list">
            <div class="add"  @click="goToMoudle({ path:'/UM/tunnelCustomer/add'})"><Icon type="plus-round"></Icon>添加</div>
            <div class="listBox">
            <div class="infoList" v-for="(item,index) in customerList" :key="index">
                <div class="index">
                    <span>{{index+1}}</span>
                </div>
                <div class="company">
                    <Icon type="star"></Icon>
                    <span>{{item.company.name}}</span>
                </div>
                <div class="payment">
                    <Icon type="social-yen-outline"></Icon>
                    <span>{{item.payTypeName}}</span>
                </div>
                <div class="">
                    <Icon type="android-person"></Icon>
                    <span>{{item.contact}}</span>
                </div>
                <div class="tel">
                    <Icon type="android-call"></Icon>
                    <span>{{item.tel}}</span>
                </div>
                <div class="crtTime">
                    <Icon type="android-time"></Icon>
                    <span>{{item.crtTime}}</span>
                </div>
                <div class="option">
                    <span @click="edit(index)">编辑</span>
                    <span>删除</span>
                </div>
            </div>
            </div>
            <!-- <Table :columns="columns1" :data="customerList"></Table> -->
            <Page :total="page.pageTotal" :current="page.pageNum" :page-size="page.pageSize" show-sizer show-total
                    placement="top" @on-change="handlePage" @on-page-size-change='handlePageSize' show-elevator style="text-align: right;margin-top: 20px;"></Page>
        </div>
    </div>
</template>
<script>
import axios from "axios";
import types from '../../../../../static/Enum.json'
export default {
    data(){
        return{
            page: {
                pageNum: 1,
                pageSize: 10,
                pageTotal: 0
            },
            customerList:[],
            conditions:{
                payment: null,
                companyId:null,
                contact: null,
                startTime: null,
                endTime: null
            },
            payType:[],
            company: []
        }
    },
    computed:{
        params() {
            let param = {
                pageNum: this.page.pageNum,
                pageSize: this.page.pageSize,
                payType: this.conditions.payment,
                companyId:this.conditions.companyId,
                contact: this.conditions.contact,
                startTime: new Date(this.conditions.startTime).getTime(),
                endTime: new Date(this.conditions.endTime).getTime()
            };
            return Object.assign({}, param);
        }
    },
    mounted(){
        //获取所有的付款方式
        axios.get('/paytype-enums').then(response=>{
            let{ code,data } = response.data
            if(code==200){
                this.payType = data;
            }
        });
        //获取所有的公司
        axios.get('/companies').then(response=>{
            let{ code,data } = response.data
            if(code==200){
                this.company = data;
            }
        })
        this.queryList()
    },
    methods:{
        queryList: function(){
            axios.post('customers/datagrid',(this.params)).then( response => {
                let { code,data } = response.data;
                if(code==200){
                    for( let index in data.list ){
                        data.list[index].crtTime = new Date( data.list[index].crtTime ).format( "yyyy-MM-dd hh:mm:s" )
                    }
                    this.customerList = data.list;
                    this.pageTotal = data.total
                }
            })
        },
        goToMoudle: function (path) {
            this.$router.push(path);
        },
        goMoudle: function (index,type) {
            this.$router.push({
                name: '添加管廊客户',
                params: {
                    id: this.customerList[index].id,
                    type: type
                }
            });
        },
        edit(index,type){
            this.goMoudle(index,types.pageType.Edit);
        },
        handlePage(value) {
            this.page.pageNum = value
            this.queryList()
        },
        handlePageSize(value) {
            this.page.pageSize = value
            this.queryList()
        },
    }
}
</script>
<style scoped>
    .infoList{
        width: 100%;
        border-bottom: 1px solid #d8d0d0;
    }
    .infoList div{
        display: inline-block;
        line-height: 45px;
        font-size: 14px;
        width: 15%;
        text-align: center;
    }
    .infoList div.index{
        width: 60px;
    }
    .conditions{
        padding: 10px 20px;
        /*background: -webkit-linear-gradient(right,#211F64, #203DA1); !* For Safari 5.1 to 6.0 *!*/
        /*background: -o-linear-gradient(right,#211F64, #203DA1); !* For Opera 11.1 to 12.0 *!*/
        /*background: -moz-linear-gradient(left,#211F64, #203DA1); !* For Firefox 3.6 to 15 *!*/
        /*background: linear-gradient(left,#107f3959, #9db7b9); !* Standard syntax *!*/
    }
    .list{
        padding: 10px 20px;
        margin-top: 10px;
        /*background: -webkit-linear-gradient(left top,#107f3959, #2A54CC); !* For Safari 5.1 to 6.0 *!*/
        /*background: -o-linear-gradient(bottom right,#222161, #2A54CC); !* For Opera 11.1 to 12.0 *!*/
        /*background: -moz-linear-gradient(bottom left,#222161, #2A54CC); !* For Firefox 3.6 to 15 *!*/
        /*background: linear-gradient(to bottom right,#107f3959, #263e7978); !* Standard syntax *!*/
    }
    .option span{
        color: red;
        cursor: pointer;
        padding: 0px 1%;
    }
    .add{
        border: 1px solid #f9e503;
        text-align: center;
        line-height: 35px;
        border-radius: 8px;
        /* margin-bottom: 20px; */
        width: 100px;
        float: right;
        cursor: pointer;
    }
    .ivu-icon{
        margin-right: 6px;
    }
    .listBox{
        margin-top: 65px;
    }
</style>



