<style scoped>
  .layout {
    border: 0px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
    background: -webkit-linear-gradient(left, #1C5D85, #286D96, #357AA1, #357AA1);
    background: -o-linear-gradient(left,#1edf17, #1edf17, #3ceded, #5d83ff);
    background: -moz-linear-gradient(left,#1edf17, #1edf17, #3ceded, #5d83ff);
    background: -ms-linear-gradient(left,#1edf17, #1edf17, #3ceded, #5d83ff);
  }
  .ivu-layout-header,.ivu-menu-light{
    background: -webkit-linear-gradient(left, #1C5D85, #286D96, #357AA1, #357AA1);
  }

  .layout-logo {
    width: 60px;
    height: 60px;
    background-image: url("../../../assets/UM/Maxview.png");
    border-radius: 3px;
    position: relative;
    float:left;
    top: 0;
  }
  .layout-title{
    float:left;
    top: 1%;
    margin-left:2%;
    color: #fff
  }

  .layout-nav {
    float:right;
    top: 1%;
    width:620px;

    /*margin-left: 67%;*/
  }
</style>
<template>
  <div class="layout">
    <Layout>
      <Header :style="{position: 'fixed', width: '100%',zIndex:1001,padding:'0',color:'#1b1754'}">
        <Menu mode="horizontal"  active-name="1" :style="{color:'#1b1754'}">
          <div class="layout-logo"></div>
         <div class="layout-title">
           <h1 >综合管廊统一管理平台</h1>
         </div>
          <div class="layout-nav">
            <MenuItem name="1">
              <Dropdown placement="bottom-start">
                <Button type="primary">
                  <Icon type="ios-navigate"></Icon>
                  运维管理
                  <Icon type="arrow-down-b"></Icon>
                </Button>
                <DropdownMenu slot="list">
                  <DropdownItem divided @click.native="goToMoudle({ path:'/UM/Patrol/homePage'})">巡检管理</DropdownItem>
                  <DropdownItem divided>检修管理</DropdownItem>
                  <DropdownItem divided @click.native="goToMoudle({ path:'/UM/defect/query' })">缺陷管理</DropdownItem>
                  <DropdownItem divided @click.native="goToMoudle({ path:'/UM/equipment/homePage'})">设备管理</DropdownItem>
                </DropdownMenu>
              </Dropdown>

            </MenuItem>
            <MenuItem name="2">
              <Dropdown placement="bottom-start">
                <Button type="primary">
                  <Icon type="ios-keypad"></Icon>
                  运营管理
                  <Icon type="arrow-down-b"></Icon>
                </Button>
                <DropdownMenu slot="list">
                  <DropdownItem divided @click.native="goToMoudle({ path:'/UM/tunnelCustomer/list'})">管廊客户管理</DropdownItem>
                   <DropdownItem divided @click.native="goToMoudle({ path:'/UM/enterGalleryApplication/query'})">入廊申请管理管理</DropdownItem>
                  <DropdownItem divided @click.native="goToMoudle({ path:'/UM/TunnelEnergy/homepage'})">管廊能耗管理</DropdownItem>
                  <DropdownItem divided @click.native="goToMoudle({ path:'/UM/OperatingSpace/tunnel/1'})">管廊空间管理</DropdownItem>

                </DropdownMenu>
              </Dropdown>
            </MenuItem>
            <MenuItem name="3">
              <Dropdown placement="bottom-start">
                <Button type="primary">
                  <Icon type="ios-analytics"></Icon>
                  综合监控
                  <Icon type="arrow-down-b"></Icon>
                </Button>
                <DropdownMenu slot="list">
                  <DropdownItem divided @click.native="goToMoudle({ path:'/UM/TunnelSupervise/list' })">管廊本体监测</DropdownItem>
                  <DropdownItem divided>管线本体监测</DropdownItem>
                  <DropdownItem divided>消防系统</DropdownItem>
                  <DropdownItem divided>通信系统</DropdownItem>
                  <DropdownItem divided>数据分析</DropdownItem>
                  <DropdownItem divided @click.native="goToMoudle({ path:'/UM/RobotMonitoring/query' })">机器人监控系统</DropdownItem>
                  <DropdownItem divided>视频监控系统</DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </MenuItem>
            <MenuItem name="4">
              <Dropdown placement="bottom-start">
                <Button type="primary">
                  <Icon type="ios-paper"></Icon>
                  应急管理
                  <Icon type="arrow-down-b"></Icon>
                </Button>
                <DropdownMenu slot="list">
                  <DropdownItem divided>基础信息管理</DropdownItem>
                  <DropdownItem divided>预案管理</DropdownItem>
                  <DropdownItem divided>调度管理</DropdownItem>
                  <DropdownItem divided>灾害管理</DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </MenuItem>
            <Dropdown>
              <a href="javascript:void(0)">
                <Badge count="1">
                  <Avatar :style="{background:'#f56a00'}">U</Avatar>
                </Badge>
              </a>
              <DropdownMenu slot="list">
                <DropdownItem>我的消息</DropdownItem>
                <DropdownItem>设置</DropdownItem>
                <DropdownItem divided @click.native="logout">注销</DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </div>
        </Menu>
      </Header>
    </Layout>
    <div :style="{marginTop:'65px',}">
      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>

  </div>
</template>
<script>
  export default {
    name: "home",
    data() {
      return {}
    },
    methods: {
      //退出登录
      logout: function () {
        var _this = this;
        this.$Modal.confirm({
          title: '注销',
          content: '<p>确认退出吗?</p>',
          onOk: () => {
            this.axios.get('/logout').then(result => {
              let {msg, code, data} = result.data;
              if (code !== "10003") {
                this.$Message.error(msg);
              } else {
                sessionStorage.removeItem('user');
                _this.$router.push('/UMlogin');
              }
            });
          },
          onCancel: () => {
          }
        });
      },
      goToMoudle(path) {
        this.$router.push(path);
      }
    }
  }
</script>



