<template>
  <Form :model="addEnterGalleryApplication" :label-width="120" @submit.native.prevent>
    <FormItem label="申请人所属公司：">
      <Input v-model="addEnterGalleryApplication.staffCompany" readonly></Input>
    </FormItem>
    <FormItem label="申请人所属部门：">
      <Input v-model="addEnterGalleryApplication.positionId" readonly></Input>
    </FormItem>
    <FormItem label="申请人：">
      <Input v-model="addEnterGalleryApplication.staffId" readonly></Input>
    </FormItem>
    <FormItem label="管廊：">
      <Input v-model="addEnterGalleryApplication.tunnelId" readonly></Input>
    </FormItem>
    <FormItem label="入廊目的：">
      <Input v-model="addEnterGalleryApplication.action" readonly></Input>
    </FormItem>
    <FormItem label="计划入廊时间：">
      <DatePicker type="datetime" placeholder="请选择计划入廊时间" v-model="addEnterGalleryApplication.preTime" readonly style="width: 560px"></DatePicker>
    </FormItem>
    <FormItem label="访客总人数：">
      <InputNumber  :min="1" v-model="addEnterGalleryApplication.visitorNumber" style="width: 560px;"></InputNumber>
    </FormItem>
    <FormItem label="访客所属公司">
      <Input v-model="addEnterGalleryApplication.visitorCompany" readonly></Input>
    </FormItem>
    <FormItem label="访客详细信息：">
      <Table border :columns="columns1" :data="addEnterGalleryApplication.visitorInfo"></Table>
    </FormItem>
    <FormItem label="审批意见：">
      <Button type="success">同意</Button>
      <Button type="error" style="margin-left: 8px">不同意</Button>
    </FormItem>
    <FormItem label="备注：">
      <Input v-model="addEnterGalleryApplication.remark" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="请输入审批备注"></Input>
    </FormItem>
    <FormItem style="text-align: center">
      <Button type="primary" @click="submitEnterGalleryApplication">提交</Button>
      <Button type="ghost"  style="margin-left: 8px">取消</Button>
    </FormItem>
  </Form>
</template>
<script>
  import axios from "axios";
  export default {
    data(){
      return{
        addEnterGalleryApplication:{
          staffCompany: '波汇科技',
          staffId: '张飒',
          tunnelId: '凤岭北路',
          action: '参观',
          preTime:  '2018-07-20 10:00:00',
          visitorNumber: 1,
          visitorCompany: '中建一局',
          positionId: '研发部',
          visitorInfo:[
            {
              id:1,
              name: '李雷',
              idCard: '370982199890782323',
              tel: '13426789634'
            },
            {
              id: 2,
              name: '韩梅梅',
              idCard: '370982199709248090',
              tel: '17665452243'
            }
          ] ,
          remark: null
        },
        columns1: [
          {
            title: '姓名',
            key: 'name',
            align: 'center'
          },
          {
            title: '身份证号',
            key: 'idCard',
            align: 'center'
          },
          {
            title: '联系方式',
            key: 'tel',
            align: 'center'
          }
        ],
      }
    },
    mounted(){

    },
    methods:{
      submitEnterGalleryApplication(){
        // this.axios.post("/req-historys", this.addEnterGalleryApplication).then(response => {
        // })
        //   .catch(function(error) {
        //     console.log(error);
        //   });
        this.$router.push("/UM/enterGalleryApplication/query");
      }
    }
  }
</script>
<style scoped>
  .ivu-form.ivu-form-label-right{
    width: 680px;
    margin: 20px auto;
  }
  ul li{
    list-style: none;
  }
  button{
    width: 80px;
  }
</style>




