<template>
  <div id="top">
    <div class="ImgResponsive lf" id="imgResponsive">
      <img class="img-responsive" src="../../../assets/VM/Maxview.png">
    </div>
    <div class="TopTitle lf"><span>{{title}}</span></div>
  </div>
</template>

<script>
    export default {
    name:'top',
    data(){
      return {
        title:"综合管廊统一管理可视化平台"
      }
    }
  }
</script>

<style >
  #top .ImgResponsive{
    width: 6%;
    height: 14%;
    position:absolute;
  }

  #top .TopTitle{
    height: 6%;
    width: 94%;
    overflow: hidden;
  }
  #top .TopTitle >span{
    font-family: inherit;
    display: inline-block;
    font-weight: 500;
    line-height: 1.1;
    margin-top: 0.5%;
    margin-bottom: 0.3%;
    color: #C1FFFF;
    font-size: 2rem;
    margin-left: 7%;
  }
</style>



