<template>
  <div class="layout bg">
    <Layout :style="{minHeight: '93vh'}">
      <Sider  :width="screenWidth*0.14" collapsible :collapsed-width="78" v-model="isCollapsed" :style="{background: '#fff'}">
        <Menu active-name="1-1" theme="light" width="auto" :class="menuitemClasses" :open-names="['1']">
         <Submenu name="1">
            <template slot="title">
              <Icon type="ios-navigate"></Icon>
              <span>管廊客户管理</span>
            </template>
            <MenuItem name="1-1" @click.native="goToMoudle({ path:'/UM/tunnelCustomer/list'})"><span>列表</span></MenuItem>
            <MenuItem name="1-2" @click.native="goToMoudle({ path:'/UM/tunnelCustomer/add'})"><span>制定</span></MenuItem>
          </Submenu>
        </Menu>
      </Sider>
      <Layout>
        <Content :style="{ padding: '24px',}" style="overflow-y: auto;overflow-x: hidden;height: 200px;">
          <router-view></router-view>
        </Content>
        <Footer class="layout-footer-center">2011-2016 &copy; TalkingData</Footer>
      </Layout>

    </Layout>
  </div>
</template>

<script>
export default {
  name: "UM_main",
  data() {
    return {
      userName: "admin",
      isCollapsed: false,
      screenWidth: 1200,
      screenHeight: 900,
    };
  },
  mounted() {
    this.screenWidth = document.body.clientWidth;
    this.screenHeight = document.body.clientHeight;
    this.goToMoudle({ path: "/UM/tunnelCustomer/list" });
  },
  computed: {
    menuitemClasses: function() {
      return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    }
  },
  methods: {
    goToMoudle(path) {
      this.$router.push(path);
    }
  }
};
</script>

<style scoped>
.layout {
    /* background: #f5f7f9; */
    position: relative;
    border-radius: 2px;
    overflow: hidden;
    /* color: #fff; */
  }
  .ivu-menu-dark.ivu-menu-vertical .ivu-menu-opened{
    background: #4E5C89;
  }
  .ivu-menu-dark.ivu-menu-vertical .ivu-menu-child-item-active>.ivu-menu-submenu-title {
    color: #D2084D !important;
  }
  .menu-item span {
    display: inline-block;
    overflow: hidden;
    width: 100px;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: bottom;
    transition: width 0.2s ease 0.2s;
  }

  .menu-item i {
    transform: translateX(0px);
    transition: font-size 0.2s ease, transform 0.2s ease;
    vertical-align: middle;
    font-size: 16px;
  }

  .collapsed-menu span {
    width: 0px;
    transition: width 0.2s ease;
  }

  .collapsed-menu i {
    transform: translateX(5px);
    transition: font-size 0.2s ease 0.2s, transform 0.2s ease 0.2s;
    vertical-align: middle;
    font-size: 22px;
  }

  .layout-footer-center {
    text-align: center;
    color: #fff;
    background: linear-gradient(#9cb6b9, #94a0bc);
  }

  h1 {
    margin: 2% 1% 0 0;
    width: 8%;
    float: left;
  }
.layout-footer-center {
  text-align: center;
  background: none;
  color: #fff;
  background: -webkit-linear-gradient(left, #1C5D85, #286D96, #357AA1, #357AA1);
}
.ivu-layout-sider >>> .ivu-layout-sider-trigger{
  background: #1C5D85;
}
.ivu-layout{
  background: #fff;
}
.ivu-layout-footer {
  padding: 13px 50px;
}
</style>



