<template>
    <Form :model="addEnterGalleryApplication" :label-width="120" @submit.native.prevent>
        <FormItem label="申请人所属公司：">
            <Select v-model="addEnterGalleryApplication.staffCompany"  @on-change="chooseDeptment()">
                <Option v-for="item in company" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
        </FormItem>
        <FormItem label="申请人所属部门：">
            <Select v-model="addEnterGalleryApplication.positionId" @on-change="chooseStaff()" :disabled="this.addEnterGalleryApplication.staffCompany==null">
                <Option v-for="(item,index) in positions" :value="item.id" :key="index">{{item.name}}</Option>
            </Select>
        </FormItem>
        <FormItem label="申请人：">
            <Select v-model="addEnterGalleryApplication.staffId" :disabled="this.addEnterGalleryApplication.staffCompany==null||this.addEnterGalleryApplication.positionId==null">
                <Option v-for="(item,index) in staffs" :value="item.id" :key="index">{{item.name}}</Option>
            </Select>
        </FormItem>
        <FormItem label="管廊：">
            <Select v-model="addEnterGalleryApplication.tunnelId">
                <Option v-for="(item,index) in tunnels" :key="index" :value="item.id">{{item.name}}</Option>
            </Select>
        </FormItem>
        <FormItem label="入廊目的：">
            <Select v-model="addEnterGalleryApplication.action">
                <Option v-for="(item,index) in action" :key="index" :value="item.val">{{item.key}}</Option>
            </Select>
        </FormItem>
        <FormItem label="计划入廊时间：">
            <DatePicker type="datetime" placeholder="请选择计划入廊时间" v-model="addEnterGalleryApplication.preTime" style="width: 560px"></DatePicker>
        </FormItem>
        <FormItem label="访客总人数：">
            <InputNumber  :min="1" v-model="addEnterGalleryApplication.visitorNumber" style="width: 560px;"></InputNumber>
        </FormItem>
        <FormItem label="访客所属公司">
            <Select v-model="addEnterGalleryApplication.visitorCompany">
                <Option v-for="item in company" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
        </FormItem>
        <FormItem label="访客详细信息："
            v-for="(item, index) in items"
                v-if="item.status"
                :key="index"
                :prop="item.name+','+item.idCard+','+item.tel">
            <Row>
                <Col span="6">
                    <Input type="text" v-model="item.name" placeholder="请输入访客姓名"></Input>
                </Col>
                <Col span="6">
                    <Input type="text" v-model="item.idCard" placeholder="请输入身份证号"></Input>
                </Col>
                <Col span="6">
                    <Input type="text" v-model="item.tel" placeholder="请输入联系方式"></Input>
                </Col>
                <Col span="4" offset="1">
                    <Button type="ghost" @click="handleRemove(index)">删除</Button>
                    <!-- <Button type="ghost">Delete</Button> -->
                </Col>
            </Row>
        </FormItem>
        <FormItem>
            <Row>
                <Col span="12">
                    <Button type="dashed" long @click="handleAdd" icon="plus-round">添加访客信息</Button>
                </Col>
            </Row>
        </FormItem>
        <FormItem style="text-align: center">
            <Button type="primary" @click="submitEnterGalleryApplication">提交</Button>
            <Button type="ghost"  style="margin-left: 8px">取消</Button>
        </FormItem>
    </Form>
</template>
<script>
import axios from "axios";
export default {
    data(){
        return{
            addEnterGalleryApplication:{
                staffCompany: null,
                staffId: null,
                tunnelId: null,
                action: null,
                preTime: null,
                visitorNumber: 1,
                visitorCompany: null,
                visitorInfo: null,
                positionId: null,
            },
            staffs:[],
            company:[],
            tunnels: [],
            action:[],
            positions:[],
            index: 1,
            items:[
                {
                    index: 1,
                    status: 1,
                    name: null,
                    idCard: null,
                    tel: null
                }
            ]
        }
    },
    mounted(){
         axios.get('/companies').then(response=>{
            let{ code,data } = response.data
            if(code==200){
                this.company=data
            }
        });
        axios.get('/tunnels').then(response=>{
            let{ code,data } = response.data
            if(code==200){
                this.tunnels = data
            }
        });
        axios.get('/action-enums').then(response=>{
            let{ code,data } =response.data
            if(code==200){
                this.action = data
            }
        });
    },
    methods:{
        handleAdd () {
            this.index++;
            this.items.push({
                index: this.index,
                status: 1,
                name:'',
                idCard:'',
                tel:''
            });
            console.log(this.addEnterGalleryApplication.staffId)
        },
        handleRemove (index) {
            this.items[index].status = 0;
        },
        submitEnterGalleryApplication(){
            var arr = new Array()
            for(let index in this.items){
                arr.push(this.items[index].name+"-"+this.items[index].idCard+"-"+this.items[index].tel)
            }
            this.addEnterGalleryApplication.visitorInfo = arr.toString()
            this.axios.post("/req-historys", this.addEnterGalleryApplication).then(response => {
                console.log(this.addEnterGalleryApplication.visitorInfo)
            })
            .catch(function(error) {
                console.log(error);
            });
            this.$router.push("/UM/enterGalleryApplication/query");
        },
        chooseDeptment(){
            this.addEnterGalleryApplication.positionId=null
            this.addEnterGalleryApplication.staffId=null
            axios.get('/companies/'+this.addEnterGalleryApplication.staffCompany+'/departments').then(response=>{
                let{ code,data } = response.data
                if(code==200){
                    this.positions = data;
                }
            })
        },
        chooseStaff(){
            this.addEnterGalleryApplication.staffId=null
            axios.get('/companies/'+this.addEnterGalleryApplication.staffCompany+'/departments/'+this.addEnterGalleryApplication.positionId+'/staffs').then(response=>{
                let{ code,data } =response.data
                if(code==200){
                    this.staffs = data
                }
            })
        },  
    }
}
</script>
<style scoped>
.ivu-form.ivu-form-label-right{
    width: 680px;
    margin: 20px auto;
}
/* ul{
    width: 400px;
} */
ul li{
    list-style: none;

}
</style>



