<template>
  <div class="Map">
    <div class=mapTop>
      <div style="width: 70%; height: 100%; padding-left: 14%;position: absolute;left: 6%">
        <row type="flex" justify="center" align="bottom">
          <Col span="1">
          <Dropdown divided @click.native="logout">
            <a> <img src="../../../assets/VM/home.png" height="40%" width="100%"></a>
          </Dropdown>
          </Col>
          <Col span="1" offset="1">
          <Dropdown divided @click.native="logout">
            <a> <img src="../../../assets/VM/user.png" height="40%" width="100%"></a>
          </Dropdown>
          </Col>
          <Col span="1" offset="1">
          <Dropdown divided @click.native="logout">
            <a> <img src="../../../assets/VM/video.png" height="40%" width="100%"></a>
          </Dropdown>
          </Col>
          <Col span="1" offset="1">
          <Dropdown divided @click.native="logout">
            <a> <img src="../../../assets/VM/genue.png" height="40%" width="100%"></a>
          </Dropdown>
          </Col>
          <Col span="1" offset="1">
          <Dropdown divided @click.native="logout" @click="showGesState">
            <a> <img src="../../../assets/VM/roboto.png" height="40%" width="100%"></a>
          </Dropdown>
          </Col>
          <Col span="1" offset="1">
          <Dropdown divided @click.native="logout">
            <a> <img src="../../../assets/VM/key.png" height="40%" width="100%"></a>
          </Dropdown>
          </Col>
          <Col span="1" offset="1">
          <Dropdown divided @click.native="logout">
            <a> <img src="../../../assets/VM/stop.png" height="15%" width="80%"></a>
          </Dropdown>
          </Col>
          <Col span="1" offset="1">
          <Dropdown divided @click.native="logout">
            <a> <img src="../../../assets/VM/fire.png" height="15%" width="80%"></a>
          </Dropdown>
          </Col>
          <Col span="1" offset="1">
          <Dropdown divided @click.native="logout">
            <a> <img src="../../../assets/VM/mobile.png" height="15%" width="80%"></a>
          </Dropdown>
          </Col>
        </row>
        <row justify="center" type="flex">
          <img src="../../../assets/VM/footLine.png" height="8px" width="85%">
        </row>
      </div>
    </div>
    <div class=mapRight>
      <temperature class=maptemperature></temperature>
      <div class="gasState">
        <div class="mapgaugeFirst">
          <map-ganue :style="{height:'100%'}" v-bind="gauge_CO">
          </map-ganue>
        </div>
        <div class="mapgaugeLast">
          <map-ganue :style="{height:'100%'}" v-bind="gauge_H2S">
          </map-ganue>
        </div>
        <div class="mapgaugeLast">
          <map-ganue :style="{height:'100%'}" v-bind="gauge_CH4">
          </map-ganue>
        </div>
        <div class="mapgaugeLast">
          <map-ganue :style="{height:'100%'}" v-bind="gauge_O2">
          </map-ganue>
        </div>
        <div class="mapgaugeLast">
          <map-ganue :style="{height:'100%'}" v-bind="gauge_Temp">
          </map-ganue>
        </div>
        <div class="mapgaugeLast">
          <map-ganue :style="{height:'100%'}" v-bind="gauge_West">
          </map-ganue>
        </div>
      </div>

    </div>
    <sm-viewer
      :id="id"
      @onload="onload">
    </sm-viewer>
  </div>
</template>
<style scoped>
  .mapTop {
    z-index: 1001;
    position: absolute;
    width: 97.3%;
    height: 7%;

  }
  .mapRight {
    z-index: 1002;
    position: absolute;
    /*margin-left: 87%;*/
    width: 8%;
    height: 68%;
    right: 2%;
  }
  .maptemperature{
    margin-top: 10%;
  }
  .mapgaugeFirst {
    width: 100%;
    height: 16%;
    position: relative;
    margin-top: 10%;
  }
  .mapgaugeLast {
    width: 100%;
    height: 16%;
    margin-top: 3%;
    position: relative;
  }
  .cesium-viewer-bottom{
    display:none
  }
  .gasState{
    position: relative;
    height: 80%;
    display: none;
  }
</style>

<script>
  import SmViewer from '../../Common/3DViewer'
  import MapGanue from '../../Common/Chart/gauge'
  import Temperature from '../VMBodyCenter/temperatureBox'
  import { URL_CONFIG } from '../../../../static/3DMap/js/3DMapConfig'

  export default {
    data() {
      return {
          msg: '地图',
          id: 'cesiumContainer',
          gauge_CO: {
          gaugeId: 'gaugeCO',
          requestUrl: 'tunnels/items/energies',
          titleName: '一氧化碳浓度',
          startColor: '#67d1ff',
          endColor: '#2eff31',
        },
          gauge_H2S: {
          gaugeId: 'gaugeH2S',
          requestUrl: 'tunnels/items/energies',
          titleName: '硫化氢浓度',
          startColor: '#edff27',
          endColor: '#ff9019',
        },
          gauge_CH4: {
          gaugeId: 'gaugeCH4',
          requestUrl: 'tunnels/items/energies',
          titleName: '甲烷浓度',
          startColor: '#51ffe7',
          endColor: '#bdbfff',
        },
          gauge_O2: {
          gaugeId: 'gaugeO2',
          requestUrl: 'tunnels/items/energies',
          titleName: '氧气浓度',
          startColor: '#67d7bc',
          endColor: '#ff1317',
        },
          gauge_Temp: {
          gaugeId: 'gaugeTemp',
          requestUrl: 'tunnels/items/energies',
          titleName: '温度',
          startColor: '#7f13ff',
          endColor: '#3ec8ff',
        },
          gauge_West: {
          gaugeId: 'gaugeWest',
          requestUrl: 'tunnels/items/energies',
          titleName: '湿度',
          startColor: '#2bff7b',
          endColor: '#0909ff',
        },
      }
    },
    components: {
      SmViewer,
      MapGanue,
      Temperature
    },
    mounted() {
    },
    methods: {
        init() {
        },
        onload(parent) {
        let Cesium = parent.Cesium;

        // 初始化viewer部件
        var viewer = new Cesium.Viewer(this.id,{
            navigation:false //关闭导航控件
        });
        var scene = viewer.scene,
             widget = viewer.cesiumWidget,
             imageryLayers = viewer.imageryLayers,
             imagery_mec;

        var provider_mec = new Cesium.SuperMapImageryProvider({
            url : URL_CONFIG.IMG_MAP//墨卡托投影地图服务
        });
         imagery_mec = imageryLayers.addImageryProvider(provider_mec);


        try{
          //打开所发布三维服务下的所有图层
          var promise = scene.open(URL_CONFIG.BIM_SCP);
          Cesium.when(promise,function(layer){

              var overGroundLayer = scene.layers._layerQueue[0];
            //设置相机位置、视角，便于观察场景
            scene.camera.setView({
              destination : new Cesium.Cartesian3.fromDegrees(116.43709465365579,39.91793569836651,1245.5482069457155),
              orientation : {
                heading : 1.5735504792701676,
                pitch : -0.9120242487858476,
                roll : 6.281841926729911
              }
            });
          },function(e){
            if (widget._showRenderLoopErrors) {
              var title = '加载SCP失败，请检查网络连接状态或者url地址是否正确？';
              widget.showErrorPanel(title, undefined, e);
            }
          });
        }
        catch(e){
          if (widget._showRenderLoopErrors) {
            var title = '渲染时发生错误，已停止渲染。';
            widget.showErrorPanel(title, undefined, e);
          }
        }
      },
        showGesState(){
            console.log('11')
        }
    }
  }

</script>



