<template>
    <div style="margin-top: 10px">
      <Row>
        <Form :model="plan" :label-width="100" @submit.native.prevent>
        <Col span="12" style="padding-right: 20px;">
          <Col span="12">
            <FormItem label="计划编号：">
              <Input v-model="uploadPlan.id"></Input>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="计划名称：">
              <Input v-model="uploadPlan.name"></Input>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="所属管廊：">
              <Select v-model="uploadPlan.tunnelId">
                <Option v-for="item in tunnels" :value="item.id" :key="item.id">{{ item.name }}</Option>
              </Select>
            </FormItem>
          </col>
          <Col span="12">
            <FormItem label="计划状态：">
              <Select v-model="uploadPlan.status">
                <Option v-for="item in tunnels" :value="item.id" :key="item.id">{{ item.name }}</Option>
              </Select>
            </FormItem>
          </col>
          <Col span="12">
            <FormItem label="责任班组：">
              <Select v-model="uploadPlan.groupId">
                <Option v-for="item in groups" :value="item.id" :key="item.id">{{ item.name }}</Option>
              </Select>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="责任人：">
              <Input v-model="groups.find(a=>a.id==uploadPlan.groupId).leaderName"></Input>
            </FormItem>
          </Col>
          <Col span="24">
            <FormItem label="计划描述：">
               <Input v-model="planDescribe" type="textarea" :autosize="{minRows: 2,maxRows: 5}"
                  placeholder="请输入计划描述"></Input>
            </FormItem>
          </Col>
        <!-- <div class="planBox">
            <span class="planDec">计划编号</span>
            <Input v-model="uploadPlan.id" placeholder="" style="width: 150px;margin-right: 15px;"></Input>
            <span class="planDec">计划名称</span>
            <Input v-model="uploadPlan.name" placeholder="" style="width: 150px;margin-right: 15px;"></Input>
        </div>

        <div class="planBox">
            <span class="planDec">所属管廊</span>
            <Select v-model="uploadPlan.tunnelId" style="width: 150px;margin-right: 15px;font-size: 14px;">
              <Option v-for="item in tunnels" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
            <span class="planDec">计划状态</span>
            <Input v-model="uploadPlan.status" placeholder="" style="width: 150px;margin-right: 15px;"></Input>
        </div>

        <div class="planBox">
            <span class="planDec">责任班组</span>
            <Select v-model="uploadPlan.groupId" style="width: 150px;margin-right: 15px;font-size: 14px;">
              <Option v-for="item in groups" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
            <span class="planDec personInCharge">责任人</span>
            <Input v-model="groups.find(a=>a.id==uploadPlan.groupId).leaderName" placeholder="" style="width: 150px;margin-right: 15px;"></Input>
        </div>
        <div class="planBox">
            <span class="planDec">计划描述</span>
            <Input v-model="planDescribe" type="textarea" :autosize="{minRows: 4,maxRows: 5}"
                  placeholder="请输入计划描述"></Input>
        </div>  -->
        </Col>
        <Col span="12">
        <div class="planBox">
          <span class="planDec">巡检日期安排</span>
          <Tabs value="name1">
              <TabPane label="间隔模式" name="name1">
                <div>
                  开始日期：
                  <input  v-model="intervalMode.startDay" type="number" min="1" max="31" value="1">
                  结束日期：<input  v-model="intervalMode.endDay" type="number" min="1" max="31" value="30">
                  间隔天数：<input  v-model="intervalMode.interval" type="number" min="1" max="31" value="1">
                  <Button type="default" @click="getDay">确定</Button>
                  <Button type="default" @click="defaultBtn">重置</Button>
                </div>
              </TabPane>
              <TabPane label="星期模式" name="name2">
                <Checkbox v-for="(item, index) in weekDay" v-model="item.value" :value='item.value' :key='index' @on-change="getWeek">{{item.name}}</Checkbox>
              </TabPane>
              <TabPane label="自定义" name="name3">
                请选择您需要的时间
              </TabPane>
          </Tabs>
          <calender ref="calender" v-on:childByValue="getActiveText"></calender>
        </div>
        <div class="planBox planBtn">
            <Button type="primary" class="btn">取消</Button>
            <Button type="primary" class="btn" @click="submitPlan">提交</Button>
        </div>
        </Col>
        </Form>
      </Row>
    </div>
</template>

<script>
import axios from "axios";
import calender from "../../../Common/calender.vue";
export default {
  name: "plans",
  components: {
    calender
  },
  data() {
    return {
      uploadPlan:{
        id: "",
        name: "",
        tunnelId: 1,
        groupId: 1,
        inspectTime: new Date(),
        tasks:[],
        status: "",
      },
      planDescribe: '',
      // workOrderStatus: "",
      groups: [{
        "id":1,
        "name":"group1",
        "leaderId":1,
        "leaderName":"l1"
      }],
      selectedMode:"",
      tunnels: [{
        "id":1,
        "name":"管廊1",
      }],
      weekDay:[
        {
          name:"星期日",
          value: false
        },
        {
          name:"星期一",
          value: false
        },
        {
          name:"星期二",
          value: false
        },
        {
          name:"星期三",
          value: false
        },
        {
          name:"星期四",
          value: false
        },
        {
          name:"星期五",
          value: false
        },
        {
          name:"星期六",
          value: false
        }
      ],
      intervalMode:{
        startDay: 1,
        endDay: 31,
        interval: 3
      }
    };
  },
  mounted() {
  //   从数据库读取所属管廊select的option选项
    axios.get("/tunnels ").then(response => {
      let { code, data } = response.data;
      if (code == 200) {
        this.tunnels = data;
      }
    });
  //     从数据库读取责任班组select的option选项
    axios.get("/inspection-groups").then(response => {
      let { code, data } = response.data;
      if (code == 200) {
        this.groups = data;
      }
    });
  },
  methods: {

    submitPlan() {
      //提交数据
      this.axios
        .post("/inspection-plans", this.uploadPlan)
        .then(response => {
          console.log(this.uploadPlan)
        })
        .catch(function(error) {
            console.log(error);
        });
      this.$router.push("/UM/patrol/query");
    },
    getDay() {
      this.$refs.calender.cancelActive();
      this.$refs.calender.interVal(this.intervalMode.startDay, this.intervalMode.endDay, this.intervalMode.interval);
      this.$refs.calender.outputHeighLight();
    },
    defaultBtn() {
      this.$refs.calender.cancelActive();
      this.$refs.calender.outputHeighLight();
    },
    getWeek(){
      this.$refs.calender.cancelActive();
      var week = [];
      for (let index = 0; index < this.weekDay.length; index++) {
        if (this.weekDay[index].value) week.push(index);
      }
      this.$refs.calender.chooseWeek(week);
      this.$refs.calender.outputHeighLight();
    },
    getActiveText(childValue){
      this.uploadPlan.tasks=[];
      for(var i=0;i<childValue.length;i++){
          this.uploadPlan.tasks.push({taskTime:childValue[i]})
      }
    }
  }
}
</script>

<style  scoped>
.planBtn {
  padding-top: 30px;
  padding-left: 307px;
}
.btn {
  margin-left: 10px;
}
input[type='number']{
    display: inline-block;
    height: 32px;
    width: 55px;
    line-height: 1.5;
    padding: 4px 7px;
    font-size: 12px;
    border: 1px solid #dddee1;
    border-radius: 4px;
    color: #495060;
    background-color: #fff;
    background-image: none;
    position: relative;
    cursor: text;
    transition: border .2s ease-in-out,background .2s ease-in-out,box-shadow .2s ease-in-out;
    margin-right: 5px;
}
</style>



