<template>
    <div>
        <Row>
            <Col span="12">
                <Form :model="equipment" :label-width="100" @submit.native.prevent>
                        <Col span="12">
                            <FormItem label="设备编号">
                                <Input v-model='equipment.assetNo' :disabled='pageType!=pageTypes.Add'></Input>
                            </FormItem>
                        </Col>
                        <Col span="12">
                            <FormItem label="设备名称">
                                <Input v-model='equipment.name' :disabled='pageType==pageTypes.Read'></Input>
                            </FormItem>
                        </Col>
                        <Col span="12">
                            <FormItem label="所属管廊">
                                <Select v-model='equipment.tunnelId' :disabled='pageType==pageTypes.Read'>
                                    <Option v-for="item in tunnels" :value="item.id" :key="item.id">{{ item.name }}</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col span="12">
                            <FormItem label="设备类型">
                                <Select v-model='equipment.type' :disabled='pageType==pageTypes.Read'>
                                    <Option v-for="(item,index) in equipmentTypes" :value="index" :key="index">{{ item.value }}</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col span="12">
                            <FormItem label="投运时间">
                                <Input v-model='equipment.crtTime' :disabled='pageType==pageTypes.Read'></Input>
                            </FormItem>
                        </Col>
                        <Col span="12">
                            <FormItem label="设备状态" >
                                <Select v-model='equipment.status' :disabled='pageType==pageTypes.Read'>
                                    <Option v-for="(item,index) in equipmentStatus" :value="item.key" :key="index">{{ item.value }}</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col span="12">
                            <FormItem label="供应商">
                                 <Select v-model='equipment.venderId'  :disabled='pageType==pageTypes.Read'>
                                    <Option v-for="(item,index) in venders" :value="item.id" :key="index">{{ item.name }}</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col span="12">
                            <FormItem label="设备型号">
                                <Select v-model='equipment.modelId'  :disabled='pageType==pageTypes.Read'>
                                    <Option v-for="(item,index) in equipmentModels" :value="item.id" :key="index">{{ item.name }}</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col span="24">
                            <FormItem label="设备位置">
                                <Input v-model='equipment.tunnelName' :disabled='pageType==pageTypes.Read'></Input>
                            </FormItem>
                        </Col>
                        <Col span="24">
                            <FormItem label="维修记录"  v-show="pageType!=pageTypes.Create">
                                <ul class="record">
                                    <li v-for="(item,index) in repairRecord" :key="index" :disabled='pageType==pageTypes.Read'>
                                        {{item.id}} : {{item.record}}
                                    </li>
                                </ul>
                                <!-- <Input v-model='repairRecord' type="textarea" :rows="5" :disabled='pageType==pageTypes.Read' v-show="pageType!=pageTypes.Create">
                                </Input> -->
                            </FormItem>
                        </Col>
                        <Col span="12">
                            <FormItem label="关联检测对象：">
                                 <Select v-model='equipment.venderId' :disabled='pageType==pageTypes.Read'>
                                    <Option v-for="(item,index) in venders" :value="item.id" :key="index">{{ item.name }}</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col span="12">
                            <FormItem label="对象类型：">
                                 <Select v-model='equipment.venderId' :disabled='pageType==pageTypes.Read'>
                                    <Option v-for="(item,index) in venders" :value="item.id" :key="index">{{ item.name }}</Option>
                                </Select>
                            </FormItem>
                        </Col>
                </Form>
            </Col>
            <Col span="12">
                <sm-viewer
                  :id="id"
                  @onload="onload">
                </sm-viewer>
            </Col>
            <Col span="22" style="text-align: right" v-show="pageType==pageTypes.Edit">
                <Button type="primary">确定</Button>
                <Button type="primary">取消</Button>
            </Col>
        </Row>
    </div>
</template>
<script>
import axios from "axios";
import types from "../../../../../static/Enum.json";
import SmViewer from '../../../../components/Common/3DViewer'
import { URL_CONFIG } from '../../../../../static/3DMap/js/3DMapConfig'
export default {
  data() {
    return {
      id: 'detailsEquipmentGIS',
      // 页面类型 1：查看 2：编辑 3：新增
      pageType: 1,
      pageTypes: types.pageType,
      // 待编辑的设备
      equipment: {
        id: 1,
        assetNo: "NO1",
        name: "设备1",
        modelId: 1,
        venderId: 1,
        type: 1,
        tunnelId: 1,
        startTime: new Date(),
        status: 1
      },
      // 设备型号
      equipmentModels: [
        {
          id: 1,
          name: "model1"
        },
        {
          id: 2,
          name: "model2"
        },
        {
          id: 3,
          name: "model3"
        }
      ],
      // 供应商
      venders: [
        {
          id: 1,
          name: "张三"
        },
        {
          id: 2,
          name: "李四"
        },
        {
          id: 3,
          name: "王五"
        }
      ],
      // 管廊
      tunnels: [],
      // 设备类型
      equipmentTypes: types.equipmentType,
      // 设备状态
      equipmentStatus: types.equipmentStatus,
      //维修记录
      repairRecord: [
        {
          id: 1,
          record: "检修一次"
        },
        {
          id: 2,
          record: "检修两次次"
        }
      ]
    };
  },
  components:{
    SmViewer
  },
  mounted() {
    this.equipment.id = this.$route.params.id;
    this.pageType = this.$route.params.type;
    console.log("pageType:", this.pageType);
    this.init();
  },
  methods: {
    init: function() {
      if (this.equipment.id == undefined) this.equipment.id = 1;

      if (this.pageType != this.pageTypes.Add) {
        // 获取设备详细信息
        axios.get("/equipments/" + this.equipment.id).then(response => {
          let { code, data } = response.data;
          if (code == "200") {
            this.equipment = data;
            this.equipment.crtTime = new Date(data.crtTime).format(
              "yyyy-MM-dd"
            );
            // this.equipment.crtTimeFormat = this.equipment.crtTime.format(
            //   "yyyy-MM-dd"
            // );
          }
        });
      }

      // 获取所有的管廊
      axios.get("/tunnels ").then(response => {
        let { code, data } = response.data;
        if (code == 200) {
          this.tunnels = data;
        }
      });

      // 获取所有的供应商
      axios.get("/venders").then(response => {
        let { code, data } = response.data;
        if (code == 200) {
          this.venders = data;
        }
      });

      // 获取所有的型号
      axios.get("/equipment-models").then(response => {
        let { code, data } = response.data;
        if (code == 200) {
          this.equipmentModels = data;
        }
      });
    },
    onload(parent) {
      let Cesium = parent.Cesium;

      // 初始化viewer部件
      var viewer = new Cesium.Viewer(this.id,{
        navigation:false //关闭导航控件
      });
      var scene = viewer.scene,
        widget = viewer.cesiumWidget,
        imageryLayers = viewer.imageryLayers,
        imagery_mec;

      var provider_mec = new Cesium.SuperMapImageryProvider({
        url : URL_CONFIG.IMG_MAP//墨卡托投影地图服务
      });
      imagery_mec = imageryLayers.addImageryProvider(provider_mec);


      try{
        //打开所发布三维服务下的所有图层
        var promise = scene.open(URL_CONFIG.BIM_SCP);
        Cesium.when(promise,function(layer){

          var overGroundLayer = scene.layers._layerQueue[0];
          //设置相机位置、视角，便于观察场景
          scene.camera.setView({
            destination : new Cesium.Cartesian3.fromDegrees(116.43709465365579,39.91793569836651,1245.5482069457155),
            orientation : {
              heading : 1.5735504792701676,
              pitch : -0.9120242487858476,
              roll : 6.281841926729911
            }
          });
        },function(e){
          if (widget._showRenderLoopErrors) {
            var title = '加载SCP失败，请检查网络连接状态或者url地址是否正确？';
            widget.showErrorPanel(title, undefined, e);
          }
        });
      }
      catch(e){
        if (widget._showRenderLoopErrors) {
          var title = '渲染时发生错误，已停止渲染。';
          widget.showErrorPanel(title, undefined, e);
        }
      }
    },
  }
};
</script>
<style scoped>
.ivu-col-span-12 {
  padding-right: 10px;
}
.ivu-col-span-24 {
  padding-right: 10px;
}
.record {
  border: 1px solid #dddee1;
  line-height: 1.5;
  padding: 4px 7px;
  border-radius: 4px;
  color: #495060;
}
.record li {
  list-style: none;
}
</style>





