<template>
  <div class="layout">
    <Layout :style="{minHeight: '93vh'}">
      <Sider :width="screenWidth*0.14" collapsible :collapsed-width="78" v-model="isCollapsed" :style="{background: '#fff'}">
        <Menu active-name="1-1" theme="light" width="auto" :class="menuitemClasses" :open-names="['1']">
          <Submenu name="1">
            <template slot="title">
              <img src="../../../../assets/UM/TunnelEnergy.png" width="5%" height="10%"/>
              <span :style="{fontSize:'20px'}">能耗管理</span>
            </template>
            <MenuItem v-for="item in leftTree" :key="item.key" :name="item.id"
                      @click.native="goToMoudle({ path:'/UM/TunnelEnergy/detials/'+item.id})">
              <span> {{ item.name}}</span>
            </MenuItem>
          </Submenu>
        </Menu>
      </Sider>
      <Layout>
        <Content :style="{ padding: '14px', background: '#fff' }" style="overflow-y: auto;overflow-x: hidden;height: 200px;">
          <router-view></router-view>
        </Content>
        <BackTop></BackTop>
        <Footer class="layout-footer-center">2003-2018 &copy; Bandweaver</Footer>
      </Layout>
    </Layout>
  </div>
</template>
<style scoped>

  .layout {
    background: #f5f7f9;
    position: relative;
    border-radius: 2px;
    overflow: hidden;
  }

  .menu-item span {
    display: inline-block;
    overflow: hidden;
    width: 100px;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: bottom;
    transition: width 0.2s ease 0.2s;
  }

  .menu-item i {
    transform: translateX(0px);
    transition: font-size 0.2s ease, transform 0.2s ease;
    vertical-align: middle;
    font-size: 16px;
  }

  .collapsed-menu span {
    width: 0px;
    transition: width 0.2s ease;
  }

  .collapsed-menu i {
    transform: translateX(5px);
    transition: font-size 0.2s ease 0.2s, transform 0.2s ease 0.2s;
    vertical-align: middle;
    font-size: 22px;
  }

  .layout-footer-center {
    text-align: center;
    background-color: #e2f0ff;
  }

  h1 {
    margin: 2% 1% 0 0;
    width: 8%;
    float: left;
  }
  .layout-footer-center {
    text-align: center;
    background: none;
    color: #fff;
    background: -webkit-linear-gradient(left, #1C5D85, #286D96, #357AA1, #357AA1);
  }
  .ivu-layout-sider >>> .ivu-layout-sider-trigger{
    background: #1C5D85;
  }
  .ivu-layout{
    background: #fff;
  }
  .ivu-layout-footer {
    padding: 13px 50px;
  }

</style>
<script>
  export default {
    name: "tunnel-energy-consumption",
    data() {
      return {
        screenWidth: 1200,
        screenHeight: 900,
        isCollapsed: false,
        leftTree: [],
      };
    },
    components: {},
    created() {
      this.leftTree = [];
      this.axios.get("/tunnels ").then(result => {
        let {code, data} = result.data;
        if (code == 200) {
          this.leftTree = data;
        }
      });

    },
    mounted() {
      this.screenWidth = document.body.clientWidth;
      this.screenHeight = document.body.clientHeight;
      this.goToMoudle({path: "/UM/TunnelEnergy/homePage"});

    },
    computed: {
      menuitemClasses: function () {
        return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
      },

    },
    methods: {
      //跳转模块
      goToMoudle(path) {
        this.$router.push(path);
      },
    }
  }
  ;
</script>





