<template>
  <div>
    <div class="conditions">
      监测仓:
      <Select v-model="conditions.tunnelId" @on-change='changeStore(conditions.tunnelId)' style="width:20vw">
        <Option v-for="item in sotres" :value="item.id" :key="item.id">{{ item.storeType.name }}</Option>
      </Select>
    </div>
    <!--<Tabs>-->
    <!--<TabPane label="GIS" icon="android-map">-->

    <div class="Section">
      <div id="pageBox" v-show="showBtn" :style="{padding: '2px', display: showSection}">
        <div>
          <div style="float: left">
            <Button type="primary" @click="toPreLi()" :disabled="btnStasus">
              <Icon type="chevron-left"></Icon>
              上一区
            </Button>
          </div>
          <div class="showSection" id="showSection" style="float: left">
            <ul id="liBox">
              <li v-for="(item,index) in sections" :key="index" @click="toArticleDetail(index)"
                  :class="{active:index==activeIndex}" :name="item.id">{{item.name}}
              </li>
            </ul>
          </div>
          <div style="float: right">
            <Button type="primary" @click="toNextLi()" :disabled="btnStasus">
              下一区
              <Icon type="chevron-right"></Icon>
            </Button>
          </div>
        </div>
      </div>
    </div>
    <diV class="gis">
      <v_3DViewer :id="id" @onload="onload">
      </v_3DViewer>
    </diV>
    <!--切换区段时显示的信息-->
    <div class="details" :style="{display:showSection}" v-for="(item,index) in sections" :key="index"
         v-if="activeIndex===index">
      <div class="tunnelsInfo">
        <div class="title">
          <h3>{{curName+item.name+"概况"}}</h3>
        </div>
      </div>
      <div class="environmentalMonitoring">
        <div class="title">环境监测</div>
        <div class="inline-box">
          <div class="numerical">温度：{{item.temperature}}</div>
          <div class="status">{{item.temperatureStatus}}</div>
          <div
            :class="{'normal': item.temperatureStatus=='正常', 'abnormal': item.temperatureStatus =='不正常'}"></div>
        </div>
        <div class="inline-box">
          <div class="numerical">湿度：{{item.humidity}}</div>
          <div class="status">{{item.humidityStatus}}</div>
          <div :class="{'normal':item.humidityStatus=='正常','abnormal': item.humidityStatus=='不正常'}"></div>
        </div>
        <div class="inline-box">
          <div class="numerical">含氧：{{item.oxygenContent}}</div>
          <div class="status">{{item.oxygenContentStatus}}</div>
          <div
            :class="{'normal':item.oxygenContentStatus=='正常','abnormal': item.oxygenContentStatus=='不正常'}"></div>
        </div>
        <div class="inline-box">
          <div class="numerical">可然气体：{{item.combustibleGas}}</div>
          <div class="status">{{item.combustibleGasStatus}}</div>
          <div
            :class="{'normal':item.combustibleGasStatus=='正常','abnormal': item.combustibleGasStatus=='偏高'}"></div>
        </div>
        <div class="inline-box">
          <div class="numerical">硫化氢：{{item.hydrothion}}</div>
          <div class="status">{{item.hydrothionStatus}}</div>
          <div :class="{'normal':item.hydrothionStatus=='正常','abnormal': item.hydrothionStatus=='不正常'}"></div>
        </div>
      </div>
      <div class="theFireWarning">
        <div class="title">消防预警</div>
        <div class="inline-box">
          <div class="numerical">状态：</div>
          <div class="status">{{item.theFireWarningStatus}}</div>
          <div :class="{'normal': item.theFireWarningStatus=='正常'}"></div>
        </div>
        <div class="inline-box">
          <div class="numerical">报警：</div>
          <div class="status">{{item.theFireWarningAlarm}}</div>
          <div :class="{'normal': item.theFireWarningAlarm == 0}"></div>
        </div>
      </div>
      <div class="monitoringSituation">
        <div class="title">监测状况</div>
        <div class="inline-box">
          <div class="numerical">状态：</div>
          <div class="status">{{item.monitoringSituationStatus}}</div>
          <div :class="{'normal': item.monitoringSituationStatus == '正常'}"></div>
        </div>
        <div class="inline-box">
          <div class="numerical">设备故障率：</div>
          <div class="status">{{item.equipmentFailureRate}}%</div>
          <div :class="{'normal': item.equipmentFailureRate<=2,'abnormal': item.equipmentFailureRate>2}"></div>
        </div>
      </div>
    </div>
    <div class="details" v-if="showSection==='none'">
      <div class="tunnelsInfo">
        <div class="tunnelProp">
          <h4>
            {{curName}}
          </h4>
          <div>
            <div style="width: 60%;float: left">
              负责人：{{curTunnel.user}}
            </div>
            <div style="width: 40%;float: left">
              监测仓个数: {{curTunnel.storeNum}}
            </div>
          </div>
          <div>
            <div style="width: 60%;float: left">
              建设单位：{{curTunnel.constructionUnit}}
            </div>
            <div style="width: 40%;float: left">
              区段数：{{curTunnel.sectionNum}}
            </div>
          </div>
          <div>
            <div>
              运营单位：{{curTunnel.operationUnit}}
            </div>
          </div>
        </div>
      </div>
      <div style="padding:4% 10% 0 10%;height:53vh; overflow-y: auto;">
        <div style="float: left;width:50%" v-for="item in Tunnelprops">
          <i-circle :percent="item.percent" :size="circleSize">
            <p>{{item.key}}</p></br>
            <p style="font-size:24px">{{item.val}}</p></br>
            <p>{{item.location}}</p>
          </i-circle>
        </div>
      </div>
    </div>
    <!--</TabPane>-->
    <!--<TabPane label="列表" icon="ios-keypad">-->
    <!--</TabPane>-->
    <!--</Tabs>-->
  </div>
</template>
<script>
  import v_3DViewer from '../../../../components/Common/3DViewer'

  export default {
    data() {
      return {
        id: "tunnnelMap",
        activeIndex: 0,
        conditions: {
          tunnelId: null
        },
        circleSize: 120,
        tunnelId: 0,
        curName: "",
        showSection: "none",
        showBtn: false,
        btnStasus: true,
        tunnels: [],
        sotres: [],
        curTunnel: {
          user: "jisin",
          storeNum: 3,
          constructionUnit: "中建一局",
          operationUnit: '波汇科技',
          sectionNum: 10,
        },
        curSotre: {
          id: '',
        },
        Tunnelprops: [],
        sections: [
          {
            id: 1,
            tunnelId: 1,
            name: '一区',
            leader: '李安',
            constructionUnit: 'XXXX',
            operationUnit: 'XXXX',
            temperature: '26',
            temperatureStatus: '正常',
            humidity: '10%',
            humidityStatus: '正常',
            oxygenContent: '35%',
            oxygenContentStatus: '正常',
            combustibleGas: '1%',
            combustibleGasStatus: '偏高',
            hydrothion: '0.2%',
            hydrothionStatus: '正常',
            theFireWarningStatus: '正常',
            theFireWarningAlarm: '0',
            monitoringSituationStatus: '正常',
            equipmentFailureRate: '0'
          },
          {
            id: 2,
            tunnelId: 1,
            name: '二区',
            leader: '张震',
            constructionUnit: 'XXXX',
            operationUnit: 'XXXX',
            temperature: '26',
            temperatureStatus: '正常',
            humidity: '10%',
            humidityStatus: '正常',
            oxygenContent: '35%',
            oxygenContentStatus: '正常',
            combustibleGas: '1%',
            combustibleGasStatus: '偏高',
            hydrothion: '0.2%',
            hydrothionStatus: '正常',
            theFireWarningStatus: '正常',
            theFireWarningAlarm: '0',
            monitoringSituationStatus: '正常',
            equipmentFailureRate: '0'
          },
          {
            id: 3,
            tunnelId: 1,
            name: '三区',
            leader: '多肉',
            constructionUnit: 'XXXX',
            operationUnit: 'XXXX',
            temperature: '26',
            temperatureStatus: '正常',
            humidity: '10%',
            humidityStatus: '正常',
            oxygenContent: '35%',
            oxygenContentStatus: '正常',
            combustibleGas: '1%',
            combustibleGasStatus: '偏高',
            hydrothion: '0.2%',
            hydrothionStatus: '正常',
            theFireWarningStatus: '正常',
            theFireWarningAlarm: '0',
            monitoringSituationStatus: '正常',
            equipmentFailureRate: '0'
          },
          {name: '四区'},
          {name: '五区'},
          {name: '六区'},
        ]
      }
    },
    watch: {
      '$route': function () {
        // $route发生变化时再次赋值
        this.tunnelId = this.$route.params.id;
        this.sotres = [];
        this.fentchData();
        this.showSection = "none";
        this.btnStasus = true;
        this.showBtn = false;
        this.setCircleSize();
      },
    },
    components: {v_3DViewer},
    mounted() {
      this.fentchData();
      this.setCircleSize();
    },
    methods: {
      onload(parent) {
        let Cesium = parent.Cesium;

        // 初始化viewer部件
        var viewer = new Cesium.Viewer(this.id, {
          navigation: false //关闭导航控件
        });

        var scene = viewer.scene;
        var widget = viewer.cesiumWidget;

        try {
          //打开所发布三维服务下的所有图层
          var promise = scene.open('http://192.168.0.41:8090/iserver/services/3D-tunnel/rest/realspace');
          Cesium.when(promise, function (layer) {

            var overGroundLayer = scene.layers._layerQueue[0];
            //设置相机位置、视角，便于观察场景
            scene.camera.setView({
              destination: new Cesium.Cartesian3.fromDegrees(116.4177913153166, 39.91471014001072, 4624.9804187965665),
              orientation: {
                heading: 1.5733340909968065,
                pitch: -0.9122717319024933,
                roll: 6.281842208585676
              }
            });
          }, function (e) {
            if (widget._showRenderLoopErrors) {
              var title = '加载SCP失败，请检查网络连接状态或者url地址是否正确？';
              widget.showErrorPanel(title, undefined, e);
            }
          });
        }
        catch (e) {
          if (widget._showRenderLoopErrors) {
            var title = '渲染时发生错误，已停止渲染。';
            widget.showErrorPanel(title, undefined, e);
          }
        }
      },
      goToMoudle(path) {
        this.$router.push(path);
      },
      setCircleSize() {
        if (window.innerWidth < 1400) {
          this.circleSize = 100;
          if (this.Tunnelprops.length > 6) {
            this.circleSize = 95
          }
        }
        else if (window.innerWidth > 1400 && window.innerWidth < 1800) {
          this.circleSize = 120;
          if (this.Tunnelprops.length > 6) {
            this.circleSize = 105
          }
        }
        else if (window.innerWidth > 1800) {
          this.circleSize = 140;
          if (this.Tunnelprops.length > 6) {
            this.circleSize = 115
          }
        }
      },
      //变更监测仓
      changeStore() {
        if (this.conditions.tunnelId) {
          this.showSection = "";
          this.btnStasus = false;
          this.showBtn = true;
          //获取区段列表
          this.axios.get("tunnels/stores/" + this.conditions.tunnelId + "/sections").then(response => {
            let {code, data} = response.data;
            if (code == 200) {
              this.sections = data.sort((a, b) => a.id - b.id);
              this.curSotre.id = this.sections[0].id;
              this.getSectionsMonitorData();
            }
          })
        }
      },
      fentchData() {
        this.tunnelId = this.$route.params.id;
        //获取管廊列表
        this.axios.get("/tunnels").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            this.tunnels = data;
            this.tunnels.forEach(a => {
              if (a.id == this.tunnelId) {
                this.curName = a.name;
              }
            });
          }
        }),

          //获取管廊详细信息
          this.axios.get("tunnels/" + this.tunnelId).then(response => {
            let {code, data} = response.data;
            if (code == 200) {
              this.curTunnel.user = data.responsibility.name;
              this.curTunnel.constructionUnit = data.construct.name;
              this.curTunnel.operationUnit = data.operation.name;
            }
          })

        //获取管仓列表
        this.axios.get("tunnels/" + this.tunnelId + "/stores").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            this.sotres = data;
          }
        })
        //获取管廊统计数据
        this.axios.get("tunnels/" + this.tunnelId + "/ai/max-cv/").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            this.Tunnelprops = [];
            data.forEach(a => {
              let temp = {};
              temp.location = a.location;
              temp.key = a.key;
              temp.val = new Number(a.val.toFixed(2));
              this.Tunnelprops.push(temp);
            })
          }
        })
        //获取管仓数
        this.axios.get("tunnels/" + this.tunnelId + "/stores/count").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            this.curTunnel.storeNum = data.val;
          }
        })
        //获取区段数
        this.axios.get("tunnels/" + this.tunnelId + "/sections/count").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            this.curTunnel.sectionNum = data.val;
          }
        })
        //获取环境监测数据
        this.axios.get("tunnels/stores/sections/" + this.tunnelId + "/ai/cv").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            // this.curTunnel.sectionNum=data.val;
          }
        })
      },

      getSectionsMonitorData() {
        this.axios.get("tunnels/stores/sections/" + this.curSotre.id + "/ai/cv").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            // this.sections=data;
          }
        })
      },

      toArticleDetail(index) {
        this.activeIndex = index;
        this.curSotre.id = document.getElementById("liBox").querySelector(".active").attributes["name"].value;
        this.getSectionsMonitorData();
      },
      toPreLi() {
        if (this.activeIndex == 0) {
          this.activeIndex = 0
        } else {
          this.activeIndex = this.activeIndex - 1;
        }
        this.curSotre.id = document.getElementById("liBox").querySelector(".active").attributes["name"].value;
        this.getSectionsMonitorData();
      },
      toNextLi() {
        if (this.activeIndex == this.sections.length - 1) {
          this.activeIndex = this.sections.length - 1
        } else {
          this.activeIndex = this.activeIndex + 1;
        }
        this.curSotre.id = document.getElementById("liBox").querySelector(".active").attributes["name"].value;
        this.getSectionsMonitorData();
      },
    },
  }
</script>
<style scoped>
  .conditions {
    margin: 10px;
  }

  .gis, .details {
    display: inline-block;
    border: 1px solid #b3b0b0;
    height: 76vh;
    vertical-align: top;
  }

  .gis {
    width: 61vw;
  }

  .details {
    border: 1px solid #b3b0b0;
    overflow-y: auto;
    width: 24.3vw;
  }

  .Section #pageBox {
    border: 1px solid #b3b0b0;
    bottom: 0px;
    width: 85.5vw;
    left: 0px;
    text-align: center;
    overflow-x: hidden;
  }

  .showSection {
    display: inline-block;
    line-height: 35px;
  }

  .showSection {
    overflow: hidden;
    padding-left: 20%;
  }

  .showSection ul li {
    display: inline-block;
    border-right: 1px solid #b3b0b0;
    line-height: 35px;
    text-align: center;
  }

  .showSection ul li {
    width: 100px;
  }

  .tunnelsInfo, .environmentalMonitoring, .theFireWarning, .monitoringSituation {
    margin: 0.5vw;
    border: 1px solid #b3b0b0;
    border-radius: 8px;
    padding-left: 10%;
    box-shadow: 5px 6px 4px rgba(0, 0, 0, .2);
    border-color: #eee;
  }

  .active {
    background: #2D8CF0;
    color: #ffffff;
  }

  .tunnelsInfo div, .environmentalMonitoring div, .theFireWarning div, .monitoringSituation div {
    line-height: 5vh;
    font-size: 14px;
  }

  .title {
    font-size: 20px !important;
    text-align: center;
    font-weight: 700;
    margin-top: 5px;
  }

  .inline-box div {
    display: inline-block;
  }

  .normal {
    width: 15px !important;
    height: 15px !important;
    border-radius: 100%;
    background: green;
  }

  .abnormal {
    width: 15px !important;
    height: 15px !important;
    border-radius: 100%;
    background: red;
  }

  .numerical {
    width: 60%;
  }

  .status {
    width: 20%;
  }
</style>



