<template>
  <div>
    <Row :gutter="16">
      <Col span="5">
      <div style="background-color: #ffce2d;min-width:120px;max-width:280px">
        <div style="position: relative; float: right">
          <Icon type="ios-arrow-thin-up " size="70" color="#fff"></Icon>
        </div>
        <div style="position: relative; float: right;color: #fff">
          <ul style="list-style-type:none; text-align: right">
            <li style="font-size: x-large">{{historyData}}</li>
            <li style="width:'10%'">千瓦时</li>
          </ul>
        </div>
        <div>
          <img src="../../../../assets/UM/TunnelEnergy.png"
               style="width:20%; height:50%;padding-left:10px;top:5px;"/>
        </div>

        <Tag color="yellow" :style="{margin:0}">历史总能耗</Tag>
      </div>
      </Col>
      <Col span="5">
      <div style="background-color: #758aff;min-width:120px;max-width:280px">
        <div style="position: relative; float: right">
          <Icon type="ios-arrow-thin-up " size="70" color="#fff"></Icon>
        </div>
        <div style="position: relative; float: right;color: #fff">
          <ul style="list-style-type:none; text-align: right">
            <li style="font-size: x-large">{{curYearData}}</li>
            <li style="width:'10%'">千瓦时</li>
          </ul>
        </div>
        <div>
          <img src="../../../../assets/UM/TunnelEnergy.png"
               style="width:20%; height:50%;padding-left:10px;top:5px;"/>
        </div>

        <Tag color="#22b0c8" :style="{margin:0}">本年度能耗</Tag>
      </div>
      </Col>
      <Col span="5">
      <div style="background-color: #1dc8b2;min-width:120px;max-width:280px">
        <div style="position: relative; float: right">
          <Icon type="ios-arrow-thin-up " size="70" color="#fff"></Icon>
        </div>
        <div style="position: relative; float: right;color: #fff">
          <ul style="list-style-type:none; text-align: right;">
            <li style="font-size: x-large">{{curMonthData}}</li>
            <li style="width:'10%'">千瓦时</li>
          </ul>
        </div>
        <div>
          <img src="../../../../assets/UM/TunnelEnergy.png"
               style="width:20%; height:50%;padding-left:10px;top:5px;"/>
        </div>

        <Tag color="#6da1ff" :style="{margin:0,fontSize:20}">本月度能耗</Tag>
      </div>
      </Col>
    </Row>
    <Row style="margin-top: 10px;">
      <Col span="8">
      <div :style="{height:'35vh'}" :id=pie.Id></div>
      </Col>
      <Col span="14" offset="2">
      <Row>
        <Col span="24">
        <div style="float:right;min-width: 485px">
          <label>起始时间：</label>
          <DatePicker v-model="startTime" type="date" placeholder="Select date" style="width:120px"></DatePicker>
          --
          <DatePicker v-model="endTime" type="date" placeholder="Select date" style="width: 120px"></DatePicker>
          <Button type="primary" icon="ios-search" size="large" @click="queryEnergies">查询</Button>
          <Button type="primary" icon="ios-download-outline" size="large" @click="exportData">导出</Button>
        </div>
        </Col>
        <Col span="22" offset="2">
        <Table size="small" stripe border :columns="tableColumn" :data="tableData" ref="table"></Table>
        <div style="float:right;margin-top: 4px">
          周期： <Select v-model="period" @on-change='changePeriod(period)' style=" width:70px">
          <Option v-for="item in periodList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        </div>
        </Col>
      </Row>
      </Col>
    </Row>
    <Row>
      <Col span="8">
      <div :style="{height:'35vh'}" :id=rader.Id></div>
      </Col>
      <Col span="14" offset="2">
      <row>
        <Col span="24">
        <LineChart :style="{height:'35vh',width:'90%'}" v-bind="lineChart" ref="childChart"></LineChart>
        </Col>
      </row>
      </Col>
    </Row>
  </div>
</template>
<style scoped>
  h1 {
    margin: 2% 1% 0 0;
    width: 8%;
    float: left;
  }

</style>
<script>
  import LineChart from '../../../../components/Common/Chart/LineChart'
  import axios from 'axios'

  export default {
    name: "tunnel-energy-consumption",
    data() {
      return {
        screenWidth: 1200,
        startTime: '',
        endTime: '',
        period: 1,
        historyData: 0,
        curYearData: 0,
        curMonthData: 0,
        periodList: [
          {
            value: 0,
            label: '日'
          },
          {
            value: 1,
            label: '周'
          },
          {
            value: 2,
            label: '月'
          },
          {
            value: 3,
            label: '年'
          },
        ],
        lineChart: {
          id: 'lineChart',
          requestUrl: 'tunnels/energies/interval/',
          titleName: '综合管廊耗电量',
          titleColor: '#030303',
          titleFontSize: 25,
          period: 1
        },
        tableColumn: [
          {
            title: '管廊名称',
            key: 'tunnelName'
          },
          {
            title: '耗电单价（元/千瓦时）',
            key: 'unitPrice'
          },
          {
            title: '耗电量（千瓦时）',
            key: 'powerConsumption'
          },
          {
            title: '耗电成本（元）',
            key: 'costing'
          }
        ],
        tableData: [
          {
            tunnelId: 1,
            tunnelName: '中山北管廊',
            unitPrice: 1.5,
            powerConsumption: 2308,
            costing: 3426
          },

          {
            tunnelId: 2,
            tunnelName: '凤岭北管廊',
            unitPrice: 1.5,
            powerConsumption: 1389,
            costing: 2083.5
          },
          {
            tunnelId: 3,
            tunnelName: '佛子岭管廊',
            unitPrice: 1.5,
            powerConsumption: 3749,
            costing: 5623.5
          },
          {
            tunnelId: 4,
            tunnelName: '长虹路管廊',
            unitPrice: 1.5,
            powerConsumption: 3749,
            costing: 5623.5
          },
          {
            tunnelId: 5,
            tunnelName: '南坡岭管廊',
            unitPrice: 1.5,
            powerConsumption: 4323,
            costing: 6484.5
          },
          {
            tunnelName: '统计',
            unitPrice: "",
            powerConsumption: 17358,
            costing: 26037
          }
        ],
        tableColumnAdd: {
          tunnelName: '统计',
          unitPrice: "",
          powerConsumption: 0,
          costing: 0
        },
        isCollapsed: false,
        tunnelList: [
          {name: "中山北管廊", max: 7000},
          {name: "凤岭北管廊", max: 7000},
          {name: "佛子岭管廊", max: 7000},
          {name: "长虹路管廊", max: 7000},
          {name: "南坡岭管廊", max: 7000}
        ],
        pieData: [],
        radarData: [],
        pie: {
          titleName: "管廊能耗统计",
          requestUrl: "tunnels/items/energies ",
          Id: "TunnelPie",
          endColor: "",
          titleColor: "#030303",
        },
        rader: {
          titleName: "能耗(KWh/km)",
          requestUrl: "tunnels/avg-energies",
          Id: "TunnelRadar",
          endColor: "",
          titleColor: "#030303",
        },
      };
    },
    components: {
      LineChart
    },
    mounted() {
      this.screenWidth = document.body.clientWidth;
      this.initPie();
      this.initRadar();
      this.refreshData();
      this.queryEnergies();
      this.initConsumption();
    },
    computed: {
      menuitemClasses: function () {
        return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
      }
    },
    methods: {
      //加载能耗数据
      initConsumption() {
        let _this = this;
        axios.all([
          _this.axios.get('/tunnels/energies/timetype/1'),
          _this.axios.get('/tunnels/energies/timetype/2'),
          _this.axios.get('/tunnels/energies/timetype/3'),
        ]).then(axios.spread(function (history, curYear, curMonth) {
          // 上面两个请求都完成后，才执行这个回调方法
          _this.historyData = history.data.data.val;
          _this.curYearData = curYear.data.data.val;
          _this.curMonthData = curMonth.data.data.val;
        }));
      },
      //修改周期
      changePeriod() {
        this.lineChart.period = this.period;
        this.$refs.childChart.fetchData(this.lineChart.requestUrl + this.lineChart.period);
      },
      //导出表数据
      exportData() {
        this.$refs.table.exportCsv({
          filename: 'Sorting and filtering data',
          original: false
        });
      },
      //查询表数据
      queryEnergies() {
        let _this = this;
        var queryParams = {startTime: null, endTime: null};
        if (_this.startTime && _this.endTime) {
          queryParams = {startTime: _this.startTime.getTime(), endTime: _this.endTime.getTime()};
        }
        _this.axios.post('/tunnels/energies/time', queryParams).then(result => {
          let {msg, code, data} = result.data;
          if (code == 200) {
            _this.tableData = [];
            _this.tableColumnAdd.powerConsumption = 0;
            _this.tableColumnAdd.costing = 0;
            data.filter(a => {
              let tempData = {};
              tempData.tunnelId = a.tunnelId;
              tempData.tunnelName = a.tunnelName;
              tempData.unitPrice = a.avgUnitPrice.toFixed(2);
              tempData.costing = a.totalPrice.toFixed(2);
              tempData.powerConsumption = a.totalValue.toFixed(2);
              _this.tableColumnAdd.powerConsumption += Number(tempData.powerConsumption, 2);
              _this.tableColumnAdd.costing += Number(tempData.costing, 2);
              _this.tableData.push(tempData);
            })
            _this.tableColumnAdd.powerConsumption = Number(_this.tableColumnAdd.powerConsumption).toFixed(2);
            _this.tableColumnAdd.costing = Number(_this.tableColumnAdd.costing).toFixed(2);
            _this.tableData.push(_this.tableColumnAdd);

          } else {
            _this.$Message.error(msg);
          }
        });
      },
      //跳转模块
      goToMoudle(path) {
        this.$router.push(path);
      },
      //加载饼图
      initPie() {
        this.drawPie(this.pie.titleName, this.pie.titleColor);
        this.fetchPieData(this.pie.requestUrl);
      },
      //加载雷达图
      initRadar() {
        this.drawRadar(this.rader.titleName, this.rader.titleColor, this.tunnelList);
        this.fetchRadarData(this.rader.titleName, this.rader.requestUrl);
      },
      //绘制雷达图
      drawRadar(titleName, titleColor, indicator) {
        let _this = this;
        _this.myChart2 = _this.$echarts.init(document.getElementById(_this.rader.Id));
        _this.myChart2.showLoading();
        let legendData = "";
        indicator.filter(function (item) {
          legendData += "," + item.name;
        })
        legendData = legendData.split(',').filter(a => a != null && a != "");
        let option = {
          title: {
            text: titleName,
            x: 'right',
            textStyle: {
              fontWeight: 'normal',
              color: titleColor,
              fontSize: '20'
            }
          },
          tooltip: {
            trigger: 'item',
          },
          legend: {
            x: 'center',
            data: legendData
          },
          radar: {
            name: {
              textStyle: {
                color: '#fff',
                backgroundColor: '#999',
                // borderRadius: 3,
                //  padding: [1, 0]
              }
            },
            indicator: indicator
          },

          series: [{
            type: 'radar',
            data: [
              {
                value: [],
                name: titleName,
                label: {
                  normal: {
                    show: true
                  },
                  emphasis: {
                    show: true
                  }
                },
                areaStyle: {
                  normal: {
                    color: { // 完成的圆环的颜色
                      colorStops: [{
                        offset: 0,
                        color: "#a0ffee" // 0% 处的颜色
                      }, {
                        offset: 1,
                        color: "#2cffcc" // 100% 处的颜色
                      }]
                    },
                  }
                }
              }
            ]
          }]
        };
        _this.myChart2.hideLoading();
        _this.myChart2.setOption(option);
        window.addEventListener("resize", this.myChart2.resize);
      },
      //绘制饼图
      drawPie(titleName, titleColor) {
        let _this = this;
        _this.myChart = _this.$echarts.init(document.getElementById(_this.pie.Id));
        _this.myChart.showLoading();

        let option = {
          title: {
            text: titleName,
            x: 'center',
            textStyle: {
              fontWeight: 'normal',
              color: titleColor,
              fontSize: '20'
            }
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c}"
          },

          series: [
            {
              name: titleName,
              type: 'pie',
              center: ['50%', '50%'],
              label: {
                normal: {
                  show: true
                },
                emphasis: {
                  show: true
                }
              },
              lableLine: {
                normal: {
                  show: true
                },
                animationType: 'scale',
                animationEasing: 'elasticOut',
                animationDelay: function (idx) {
                  return Math.random() * 200;
                },
                emphasis: {
                  show: true
                }
              },
              data: _this.pieData,
            }
          ]
        };

        _this.myChart.hideLoading();
        _this.myChart.setOption(option);
        window.addEventListener("resize", this.myChart.resize);
      },
      //刷新饼图数据
      fetchPieData(requestUrl) {
        let _this = this;
        _this.axios.get(requestUrl).then(result => {
          let {code, data} = result.data;
          if (code == 200) {
            var tempCount = 0;
            data.filter(function (item) {
              tempCount += item.val;
            })
            let newData = data.map(curObj => {
              return {
                value: curObj.val,
                name: curObj.key + " " + (100 * curObj.val / tempCount).toFixed(2) + "%"
              }
            })
            if (JSON.stringify(newData) != JSON.stringify(data)) {
              _this.pieData = newData
              _this.myChart.setOption({
                series: {data: _this.pieData}
              })
            }
          }

        })
      },
      //刷新雷达图数据
      fetchRadarData(titleName, requestUrl) {
        let _this = this;
        _this.axios.get(requestUrl).then(result => {
          let {code, data} = result.data;
          if (code == 200) {
            let newData = [];
            data.filter(item => {
              newData.push(item.val.toFixed(2))
            });
            for (let i = 0; i < data.length; i++) {
              _this.tunnelList[i].name = data[i].key;
            }
            if (JSON.stringify(data) != null) {
              _this.radarData = newData;

              _this.myChart2.setOption({
                series: [{
                  name: titleName,
                  type: 'radar',
                  data: [
                    {
                      value: newData,
                      name: titleName,
                      areaStyle: {
                        normal: {
                          opacity: 0.9,
                          color: { // 完成的圆环的颜色
                            colorStops: [{
                              offset: 0,
                              color: "rgba(160,255,238,0.3)" // 0% 处的颜色
                            }, {
                              offset: 1,
                              color: "rgba(44,255,204,1)"// 100% 处的颜色
                            }]
                          },
                        }
                      }
                    }
                  ]
                }],
                radar: {
                  name: {
                    textStyle: {
                      color: '#fff',
                      backgroundColor: '#999',
                      borderRadius: 3,
                      padding: [3, 5]
                    }
                  },
                  indicator: _this.tunnelList
                },
              })
            }
          }

        })
      },
      //定时刷新数据
      refreshData() {
        let _this = this;
        setInterval(() => {
          _this.fetchPieData(_this.pie.requestUrl),
            _this.fetchRadarData(_this.rader.titleName, _this.rader.requestUrl)
        }, 50000)
      },
    }
  }
  ;
</script>





