<template>
    <div>
      <h1>管廊设备信息总览</h1>
      <Row>
        <Col span="16">
          <Row>
            <Col span="24">
              <div class="GISbox">
                <div class="GISTitle">地图显示所有的管廊位置GIS</div>
                <sm-viewer
                  :id="id"
                  @onload="onload">
                </sm-viewer>
                <div class="tunnelProfile">
                  <div>设备信息概况</div>
                  <div>设备数：100</div>
                  <div>管廊1：70</div>
                  <div>管廊2：30</div>
                  <div>故障数：5</div>
                </div>
              </div>
            </Col>
            <Col span="11" style="margin-top: 5px">
              <Card>
                <p slot="title">故障信息列表</p>
                <p v-for="(item,index) in faultInfoList" value="item" :key="index">{{item.list}}</p>
              </Card>
            </Col>
            <Col span="11" offset="2" style="margin-top: 5px">
              <Card>
                <p slot="title">故障信息列表</p>
                <p v-for="(item,index) in faultInfoList" value="item" :key="index">{{item.list}}</p>
              </Card>
            </Col>
          </Row>
        </Col>
        <Col span="8">
        <Row>
          <Col span="12"><div class="data-box" style="margin-right: 0"><DataShow v-bind="equimentTotalNum"></DataShow></div></Col>
          <Col span="12"><div class="data-box"><DataShow v-bind="equimentRunNum"></DataShow></div></Col>
          <Col span="12"><div class="data-box" style="margin-right: 0"><DataShow v-bind="spareAFew"></DataShow></div></Col>
          <Col span="12"><div class="data-box"><DataShow v-bind="duration"></DataShow></div></Col>
        </Row>
        </Col>
      </Row>
      <Row style="padding-top: 5px;">
        <Col span="7">
            <div style="width: 24vw; height:22vh;" class="equipmentChartBox">
              <simple-bar v-bind="equipmentChart"></simple-bar>
          </div>
        </Col>
        <Col span="7">
            <div style="width:24vw; height:22vh;" class="equipmentChartDoubleColor">
                <simple-pie v-bind="equipmentChartDoubleColor"></simple-pie>
            </div>
        </Col>
        <Col span="5">
            <div class="circleBox" style="background: #2C6A69;">
                <i-circle :size="200"
                          :trail-width="4"
                          :stroke-width="8"
                          :percent="75"
                          stroke-linecap="square"
                          stroke-color="#fff" style="margin-top: 5px;">
                  <p class="demo-Circle-inner" style="font-size:22px;color: #fff">已完成任务占比</p>
                  <p class="demo-Circle-inner" style="font-size: 22px;color: #fff">75%</p>
                </i-circle>
            </div>
        </Col>
        <Col span="5">
            <div class="circleBox" style="background: #D5C7E1">
                <i-circle :size="200"
                          :trail-width="4"
                          :stroke-width="8"
                          :percent="25"
                          stroke-linecap="square"
                          stroke-color="#E5B500" style="margin-top: 5px;">
                  <p class="demo-Circle-inner" style="font-size:22px">故障占比</p>
                  <p class="demo-Circle-inner" style="font-size: 22px">25%</p>
                </i-circle>
            </div>
        </Col>
      </Row>
    </div>
</template>

<script>
import DataBox from '../../../../components/Common/DataBox'
import DataShow from '../../../../components/Common/DataShow'
import SimpleBar from '../../../../components/Common/Chart/SimpleBarChart'
import SimplePie from '../../../../components/Common/Chart/SimplePieChart'
import Ring from '../../../../components/Common/Chart/RingChart'
import SmViewer from '../../../../components/Common/3DViewer'
import { URL_CONFIG } from '../../../../../static/3DMap/js/3DMapConfig'
// import FeatureSample from '../../../../components/Common/Chart/FeatureSample'
export default {
    name: 'equipmentMain',
    data() {
        return {
            id:'GIS_ID',
            equimentTotalNum:{
                label: "年度计划数",
                value: 100,
                imgSrc: "../../../static/UM/annualPlan.png",
                imgBac: "#8C91A7",
            },
            equimentRunNum:{
                label: "本月计划",
                value: 87,
                imgSrc: "../../../static/UM/curMonthPlan.png",
                imgBac: "#7ca9a9",
            },
            spareAFew:{
                label: '已完成巡检任务数',
                value: '5',
                imgSrc: '../../../static/UM/processedPlan.png',
                imgBac: '#112f6b',
            },
            duration:{
                label: '待巡检任务',
                value: '123456h',
                imgSrc: '../../../static/UM/pendingPlan.png',
                imgBac: '#BD4932',
            },
            equipmentChart:{
                id:"equipmentChartId",
                requestUrl:"/equipments/type/count",
                title:"各类型设备数"
            },
            equipmentChartDoubleColor:{
                id:"equipmentCharDoubleColortId",
                requestUrl:"/equipments/type/count",
                title:"各类型故障占比"
            },
            equimentPie:{
                id:"equipmentPieId",
                requestUrl:"/equipments/type/count",
                title:"各类型故障占比"
            },
            processedPlan: {
                id: 'processedPlan',
                explain: '已完成任务占比'
            },
            breakDown:{
                id: 'breakDown',
                explain: '故障占比'
            },
            faultInfoList:[
                {list: '设备1故障信息'},
                {list: '设备10故障信息'},
                {list: '设备1故障信息'},
                {list: '设备10故障信息'},
                {list: '设备1故障信息'},
                {list: '设备10故障信息'},
                {list: '设备1故障信息'},
                {list: '设备10故障信息'},
                {list: '设备1故障信息'},
                {list: '设备10故障信息'},
                {list: '设备1故障信息'},
                {list: '设备10故障信息'},
            ],
            tunnelInfoList:[
                {list: '管廊1设备总数70，损坏3'},
                {list: '管廊1设备总数70，损坏3'},
                {list: '管廊1设备总数70，损坏3'},
                {list: '管廊1设备总数70，损坏3'},
                {list: '管廊1设备总数70，损坏3'},
                {list: '管廊1设备总数70，损坏3'}
            ],
        }
    },
    components: {
        DataBox,
        SimpleBar,
        SimplePie,
        Ring,
        DataShow,
        SmViewer
        // FeatureSample
    },
    mounted(){
    },
    methods:{
        goToMoudle: function (path) {
            this.$router.push(path);
        },
        onload(parent) {
        let Cesium = parent.Cesium;

        // 初始化viewer部件
        var viewer = new Cesium.Viewer(this.id,{
          navigation:false //关闭导航控件
        });
        var scene = viewer.scene,
          widget = viewer.cesiumWidget,
          imageryLayers = viewer.imageryLayers,
          imagery_mec;

        var provider_mec = new Cesium.SuperMapImageryProvider({
          url : URL_CONFIG.IMG_MAP//墨卡托投影地图服务
        });
        imagery_mec = imageryLayers.addImageryProvider(provider_mec);


        try{
          //打开所发布三维服务下的所有图层
          var promise = scene.open(URL_CONFIG.BIM_SCP);
          Cesium.when(promise,function(layer){

            var overGroundLayer = scene.layers._layerQueue[0];
            //设置相机位置、视角，便于观察场景
            scene.camera.setView({
              destination : new Cesium.Cartesian3.fromDegrees(116.43709465365579,39.91793569836651,1245.5482069457155),
              orientation : {
                heading : 1.5735504792701676,
                pitch : -0.9120242487858476,
                roll : 6.281841926729911
              }
            });
          },function(e){
            if (widget._showRenderLoopErrors) {
              var title = '加载SCP失败，请检查网络连接状态或者url地址是否正确？';
              widget.showErrorPanel(title, undefined, e);
            }
          });
        }
        catch(e){
          if (widget._showRenderLoopErrors) {
            var title = '渲染时发生错误，已停止渲染。';
            widget.showErrorPanel(title, undefined, e);
          }
        }
      },
    },
}
</script>
<style scoped>
    .ivu-col-span-12{
        padding: 10px;
    }
  h1,.ivu-card >>> .ivu-card-head p,.GISTitle{
    color: #2e739b;
  }
  .GISbox{
    width: 100%;
    height: 43vh;
    border: 1px solid #dddee1;
    display: inline-block;
    vertical-align: top;
    margin-top: 10px;
    border-radius: 4px;
  }
  .GISTitle{
    background-color: #f7f8f9;
    font-size: 18px;
    line-height: 35px;
    padding-left: 5px;
    z-index: 2;
    position: absolute;
    width: 100%;
  }
    .tunnelProfile{
      border-bottom: 1px solid #dddee1;
      position: absolute;
      bottom: 10px;
      right: 10px;
    }
    .tunnelProfile div{
      border: 1px solid #dddee1;
      border-bottom: none;
      line-height: 35px;
      text-align: center;
      padding-left: 10px;
      padding-right: 10px;
    }
    .circleBox{
        text-align: center;
        height: 22vh;
        width: 98%;
        display: inline-block;
        vertical-align: middle;
        border: 1px solid #dddee1;
        border-radius: 4px;
        background: #eeeeee;
    }
    .equipmentChartBox,.equipmentChartDoubleColor{
      border: 1px solid #dddee1;
      border-radius: 4px;
      height: 22vh;
      width: 99%;
    }
  .ivu-card >>> .ivu-card-body{
      height: 9vh;
      overflow-y: auto;
  }
    .cesium-viewer-bottom{
      display: none;
    }
</style>





